.SwitchOn {
	display: block;
	padding: 8px 15px;
}

.SwitchOn__self {
	display: none;
}

.SwitchOn__pseudo {
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	position: relative;
	display: block;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-tap-highlight-color: transparent;
}

.SwitchOn__pseudo::before {
	position: absolute;
	content: '';
}

.SwitchOn__self:checked + .SwitchOn__pseudo::before {
	transform: translateY(20px);
}



/**
 * iOS
 */
.SwitchOn--ios,
.SwitchOn--android {
	margin: -12px -15px;
}

.SwitchOn--ios .SwitchOn__pseudo,
.SwitchOn--android .SwitchOn__pseudo {
	width: 60px;
	height: 100px;
	border: 2px solid var(--switch_ios_off_border);
	background: transparent;
	border-radius: 30px;
	box-sizing: border-box;
	transition: background-color .2s ease, border-color .2s ease;
}

.SwitchOn--ios .SwitchOn__self:checked + .SwitchOn__pseudo,
.SwitchOn--android .SwitchOn__self:checked + .SwitchOn__pseudo {
	border-color: var(--accent);
	background: var(--accent);
}

.SwitchOn--ios .SwitchOn__self[disabled] + .SwitchOn__pseudo,
.SwitchOn--android .SwitchOn__self[disabled] + .SwitchOn__pseudo {
	opacity: .4;
}

.SwitchOn--ios .SwitchOn__pseudo::before,
.SwitchOn--android .SwitchOn__pseudo::before {
	top: 40px;
	left: 1px;
	width: 54px;
	height: 54px;
	border-radius: 27px;
	background: var(--white);
	box-shadow: 0 3px 8px rgba(0, 0, 0, .15), 0 3px 1px rgba(0, 0, 0, .06), inset 0 0 0 .5px rgba(0, 0, 0, .04);
	transition: transform .2s cubic-bezier(.36, -.24, .26, 1.32);
}

.SwitchOn--ios .SwitchOn__self:checked + .SwitchOn__pseudo::before,
.SwitchOn--android .SwitchOn__self:checked + .SwitchOn__pseudo::before {
	transform: translateY(-40px);
}